"use server";

import { checkAvailability, saveBooking, Booking } from "@/lib/db-local";
import { addMyStudioAction, getUserById } from "./user";
import { Resend } from 'resend';

const resend = new Resend(process.env.RESEND_API_KEY);

// Update interface
interface BookingRequest {
    userId: string;
    studioId: string;
    roomName?: string;
    date: string;
    startTime: string;
    durationHours: number;
    userCount: number;
    equipmentIds: string[];
    totalPriceOverride?: number; // Allow client calculated price
}

// ... constants ...
const PRICE_BAND_HOURLY = 2500;
const PRICE_INDIVIDUAL_HOURLY = 800;
const PRICE_LOCKOUT_FLAT = 20000;

interface BookingResponse {
    success: boolean;
    message: string;
    price?: number;
    bookingId?: string;
}

export async function createBooking(data: BookingRequest): Promise<BookingResponse> {
    const { userId, studioId, roomName, date, startTime, durationHours, userCount, totalPriceOverride } = data;

    // ... availability check ...

    // 1. INVENTORY CHECK (Double Booking Prevention)
    const isAvailable = checkAvailability(studioId, roomName, date, startTime, durationHours);
    if (!isAvailable) {
        return {
            success: false,
            message: "üö´ This time slot is already booked! Please choose another time."
        };
    }

    // 2. Calculate Price
    let totalPrice = 0;
    if (totalPriceOverride !== undefined) {
        totalPrice = totalPriceOverride;
    } else {
        // Fallback to legacy logic
        const bookingDate = new Date(date);
        const now = new Date();
        const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
        const tomorrow = new Date(today);
        tomorrow.setDate(tomorrow.getDate() + 1);
        const targetDate = new Date(bookingDate.getFullYear(), bookingDate.getMonth(), bookingDate.getDate());
        const isTodayOrTomorrow = targetDate.getTime() === today.getTime() || targetDate.getTime() === tomorrow.getTime();

        if (durationHours >= 10) {
            totalPrice = PRICE_LOCKOUT_FLAT;
        }
        else if (userCount <= 2 && isTodayOrTomorrow) {
            totalPrice = PRICE_INDIVIDUAL_HOURLY * userCount * durationHours;
        }
        else {
            totalPrice = PRICE_BAND_HOURLY * durationHours;
        }
    }

    // 3. Save to Local DB
    const newBooking: Booking = {
        id: Math.random().toString(36).substring(7),
        userId,
        studioId,
        roomName,
        date,
        startTime,
        durationHours,
        userCount,
        totalPrice,
        createdAt: new Date().toISOString()
    };

    saveBooking(newBooking);

    // 3.5 Auto-register to My Studios
    try {
        await addMyStudioAction(studioId);
    } catch (e) {
        console.error("Failed to auto-add my studio", e);
    }

    // 4. Notifications
    // fetch user for email
    const user = await getUserById(userId);
    const userEmail = user?.email;

    if (process.env.RESEND_API_KEY && userEmail) {
        try {
            await resend.emails.send({
                from: 'Studi-Go <noreply@send.studi-go.com>',
                to: userEmail,
                subject: '„ÄêMusic Studio„Äë‰∫àÁ¥ÑÂÆå‰∫Ü„ÅÆ„ÅäÁü•„Çâ„Åõ',
                html: `
                    <h1>‰∫àÁ¥Ñ„ÅåÂÆå‰∫Ü„Åó„Åæ„Åó„Åü</h1>
                    <p>‰ª•‰∏ã„ÅÆÂÜÖÂÆπ„Åß‰∫àÁ¥Ñ„ÇíÂèó„Åë‰ªò„Åë„Åæ„Åó„Åü„ÄÇ</p>
                    <ul>
                        <li><strong>‰∫àÁ¥ÑID:</strong> ${newBooking.id}</li>
                        <li><strong>„Çπ„Çø„Ç∏„Ç™:</strong> ${studioId}</li>
                        <li><strong>ÈÉ®Â±ã:</strong> ${roomName}</li>
                        <li><strong>Êó•‰ªò:</strong> ${date}</li>
                        <li><strong>ÊôÇÈñì:</strong> ${startTime} (${durationHours}ÊôÇÈñì)</li>
                        <li><strong>ÊñôÈáë:</strong> ¬•${totalPrice.toLocaleString()}</li>
                    </ul>
                    <p>„ÅîÊù•Â∫ó„Çí„ÅäÂæÖ„Å°„Åó„Å¶„Åä„Çä„Åæ„Åô„ÄÇ</p>
                `
            });
            console.log(`[Notification] üìß Email sent to ${userEmail} via Resend.`);
        } catch (error) {
            console.error('[Notification] ‚ùå Failed to send email via Resend:', error);
        }
    } else {
        console.log(`[Notification] ‚ö†Ô∏è Skipped email sending (Missing API Key or User Email).`);
        console.log(`[Notification] üìß Simulated Email to User (${userId}): Booking Confirmed! ID: ${newBooking.id}`);
    }

    console.log(`[Notification] üì± (Simulated) Sending SMS to User (${userId}): Reminder for ${date} set.`);
    console.log(`[Notification] üè™ (Simulated) Sending Notification to Studio (${studioId}): New Booking Request.`);

    return {
        success: true,
        message: "Booking successful! Confirmation sent to email/SMS.",
        price: totalPrice,
        bookingId: newBooking.id
    };
}
